function scrollingProgressSyncInit(navItems) {
    const $window = $(window);

    let scrollingPoints = null;

    function setScrollingPoints() {
        let element, elementTopOffset;

        if ( !scrollingPoints ) {
            // create scrollingPoints
            scrollingPoints = [];
            $.each(navItems, (index, item) => {
                if (item) {
                    element = $(item);
                    if (element && element.length) {
                        elementTopOffset = element.offset().top;

                        scrollingPoints.push({
                            start: Math.floor(elementTopOffset),
                            end: Math.floor(elementTopOffset + element.outerHeight(true)),
                            element: element,
                        });
                    }
                }
            });
        } else {
            // update scrollingPoints
            $.each(scrollingPoints, (index, item) => {
                item.start = Math.floor(item.element.offset().top);
                item.end = Math.floor(item.start + item.element.outerHeight(true));
            });
        }
    }

    let activePointIndex = false;
    function scrollingProgressSync() {
        let centerPoint = $window.scrollTop() + $window.height() / 2,
            point;

        function setNewPoint() {
            for(let pointIndex in scrollingPoints) {
                const point = scrollingPoints[pointIndex];
                if ((centerPoint >= point.start) && (centerPoint < point.end)) {
                    point.element.addClass("active");
                    return pointIndex;
                }
            }
            return false;
        }

        function checkCurrentPoint( activePointIndex ) {
            const point = scrollingPoints[activePointIndex];
            if ((centerPoint < point.start) || (centerPoint >= point.end)) {
                point.element.removeClass("active");
                return setNewPoint();
            }
            return activePointIndex;
        }

        if ( activePointIndex ) {
            activePointIndex = checkCurrentPoint( activePointIndex );
        } else {
            activePointIndex = setNewPoint();
        }
    }
    
    $window.on("resize", setScrollingPoints);
    $window.on("scroll", scrollingProgressSync);
    setScrollingPoints();
    scrollingProgressSync();
}

export default () => {
    scrollingProgressSyncInit(['#step-1', '#step-2', '#step-3', '#step-4']);
};


