import scrollingNavSyncInit from './scrollingNavSync';

function headerScrolledTriggerInit() {
    const $header = $("#main-header"),
          $window = $(window);

    function triggerHeaderFixed() {
        let st = $window.scrollTop();

        if (st > 100) {
            $header.addClass("header--fixed");
        } else {
            $header.removeClass("header--fixed");
        }
    }

    $window.on("scroll", triggerHeaderFixed);
    triggerHeaderFixed();
}

function burgerMenuInit() {
    const $burgerBtn = $("#burger-menu"),
          $burgerMenu = $("#burger-btn");
        
    $burgerMenu.on("click", () => {
        $burgerBtn.toggleClass("active");
        $burgerMenu.toggleClass("active");
        return false;
    });

    $("body").on("click", ":not(#burger-btn, #burger-menu)", () => {
        $burgerBtn.removeClass("active");
        $burgerMenu.removeClass("active");
    });
}

export default () => {
    headerScrolledTriggerInit();
    burgerMenuInit();
    scrollingNavSyncInit($('a.header-nav__item-link[href^="#"]'));
}




