function normalizeSerializedArray(serializedArray) {
    let normalizedData = new Object;
    $.each(serializedArray, (idx, obj) => {
        normalizedData[obj.name] = obj.value;
    });
    return normalizedData;
}

export default () => {
    const $globalLoading = $("#global-loading");
    const $contactForm = $("#contact-form");

    $contactForm.on("submit", (event) => {
        event.preventDefault();
        const data = normalizeSerializedArray($contactForm.serializeArray());
        $.ajax({
            url: $contactForm.attr("action"),
            type: 'POST',
            dataType: 'JSON',
            data: data,
            beforeSend(data) {
                $globalLoading.fadeIn(320); 
            },
            complete(data) {
                $globalLoading.fadeOut(320); 
            },
            success(data) {
                const title = (data.status === 'error') ? '' : '';
                window.popupObj.createOrChange({
                    popupId: 'alert-popup',
                    title: title,
                    content: window.popupObj.renderErrorsList([data.message || data.statusText]),
                });
                
                window.popupObj.show('alert-popup');
            },
            error(data) {
                window.popupObj.createOrChange({
                    popupId: 'alert-popup',
                    title: '',
                    content: window.popupObj.renderErrorsList([data.message || data.statusText]),
                });
                
                window.popupObj.show('alert-popup');
            },
        })
        return false;
    });
};


