function setHash( hash ) {
    if (history.pushState) {
        history.pushState(null, null, hash);
    } else {
        location.hash = hash;
    }
}

function smoothScrollingToHashInit() {
    const $root = $('html, body');
    
    $('[href^="#"]').on("click", function(event) {
        event.preventDefault();
        let $this = $(this),
            hash = $this.attr("href");

        if (hash && hash.substr(1)) {
            let $elem = $(hash);
            if ( $elem && $elem.length && !$this.hasClass("active") ) {
                $root.animate({
                    scrollTop: $elem.offset().top
                }, 1000, function() {
                    setHash(hash);
                });
            }
        }
    });
}

export default smoothScrollingToHashInit;


