import {
    Swiper,
    Scrollbar,
    Autoplay,
} from 'swiper/dist/js/swiper.esm.js';

Swiper.use([Scrollbar, Autoplay]);

function initProjectsSwiper() {
    const $window = $(window);
    let projectsSwiper;
    projectsSwiper = new Swiper('#projects-swiper', {
        slidesPerView: 'auto',
        spaceBetween: 24,
        speed: 360,
        centeredSlides: false,
        grabCursor: true,
        scrollbar: {
            el: '#projects-swiper .swiper-scrollbar',
            hide: false,
            draggable: true
        },
        breakpoints: {
            575: {
                centeredSlides: true,
            },
        }
    });
    $window.on("resize", () => {
        projectsSwiper.update();
    });
}

export default initProjectsSwiper;


