function parallaxLayersInit() {
    const $window = $(window);
    const startBackLayer = $("#start-back-layer")[0];
    const requestAnimationFrame = window.requestAnimationFrame || window.mozRequestAnimationFrame ||
                                  window.webkitRequestAnimationFrame || window.msRequestAnimationFrame;
    
    function parallaxTranslate() {
        let translateY = $window.scrollTop() * 0.35;

        startBackLayer.style = `transform: translateY(${translateY}px)`;
        requestAnimationFrame(parallaxTranslate);
    }

    // $window.on("scroll", parallaxTranslate);

    requestAnimationFrame(parallaxTranslate);
}

export default parallaxLayersInit;


