import {
    Swiper,
    Pagination,
    Autoplay,
} from 'swiper/dist/js/swiper.esm.js';

Swiper.use([Pagination, Autoplay]);

function initAboutSwiper() {
    new Swiper('#about-swiper', {
        loop: true,
        autoplay: {
            delay: 10000,
            disableOnInteraction: false,
        },
        slidesPerView: 1,
        spaceBetween: 24,
        speed: 360,
        centeredSlides: true,
        grabCursor: true,
        pagination: {
            el: '#about-swiper .swiper-pagination',
            clickable: true,
        },
    });
}

export default initAboutSwiper;


