// global style
import "./index.scss";

// polyfills
import "babel-polyfill";
import requestanimationframe from './polyfills/requestanimationframe';

// helpers
import smoothScrollingToHashInit from './helpers/smoothScrollingToHash';

// components
import TextField from './components/TextField/TextField';
import Popup from './components/Popup/Popup';

// blocks
import headerInit from './blocks/header/header';
import startInit from './blocks/start/start';
import stepsInit from './blocks/steps/steps';
import aboutInit from './blocks/about/about';
import projectsInit from './blocks/projects/projects';
import loadingInit from './blocks/loading/loading';
import contactFormInit from './blocks/contact-form/contact-form';

function popupInit() {
    if ( !window.popupObj ) {
        window.popupObj = new Popup();
    }
    window.popupObj.init();
}

loadingInit();
$(() => {
    popupInit();
    smoothScrollingToHashInit();
    headerInit();
    startInit();
    stepsInit();
    aboutInit();
    projectsInit();
    contactFormInit();
    new TextField(".input--text");
});



